.window {
    user-select: none;
    background: #fff;
    width: 75vw;
    margin: auto;
    border: 1px solid #acacac;
    border-radius: 6px;
    box-shadow: 0px 0px 20px black;

    span {
        line-height: 9px;
        vertical-align: 50%;
    }

    .content {
        padding: 10px;
    }

    .titlebar {
        background: linear-gradient(top, #ebebeb, #d5d5d5);
        color: #4d494d;
        font-size: 11pt;
        line-height: 20px;
        text-align: center;
        width: 100%;
        height: 20px;
        border-top: 1px solid #f3f1f3;
        border-bottom: 1px solid #b1aeb1;
        border-top-left-radius: 6px;
        border-top-right-radius: 6px;
        cursor: default;

        .buttons {
            padding-left: 8px;
            padding-top: 3px;
            margin-right: -60px;
            float: left;
            line-height: 0px;
        }

        .buttons:hover a {
            visibility: visible;
        }

        .close {
            background: #ff5c5c;
            font-size: 9pt;
            width: 11px;
            height: 11px;
            border: 1px solid #e33e41;
            border-radius: 50%;
            display: inline-block;
        }

        .close:active {
            background: #c14645;
            border: 1px solid #b03537;
        }

        .close:active .closebutton {
            color: #4e0002;
        }

        .closebutton {
            color: #820005;
            visibility: hidden;
            cursor: default;
        }

        .minimize {
            background: #ffbd4c;
            font-size: 9pt;
            line-height: 11px;
            margin-left: 4px;
            width: 11px;
            height: 11px;
            border: 1px solid #e09e3e;
            border-radius: 50%;
            display: inline-block;
        }

        .minimize:active {
            background: #c08e38;
            border: 1px solid #af7c33;
        }

        .minimize:active .minimizebutton {
            color: #5a2607;
        }

        .minimizebutton {
            color: #9a5518;
            visibility: hidden;
            cursor: default;
        }

        .zoom {
            background: #00ca56;
            font-size: 9pt;
            line-height: 11px;
            margin-left: 6px;
            width: 11px;
            height: 11px;
            border: 1px solid #14ae46;
            border-radius: 50%;
            display: inline-block;
        }

        .zoom:active {
            background: #029740;
            border: 1px solid #128435;
        }

        .zoom:active .zoombutton {
            color: #003107;
        }

        .zoombutton {
            color: #006519;
            visibility: hidden;
            cursor: default;
        }
    }
}